.todo {
  border: 1px solid rgba(31, 31, 41, 0.7);
  border-radius: 10px;
  padding: 10px;
}

.todo:not(:last-child) {
  margin: 0px 0px 10px 0px;
}

.todo__header {
  font-weight: bold;
}

.todo__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todo__content {
  overflow-x: auto;
  position: relative;
}

.completed {
  text-decoration: line-through;
}

.buttons {
  display: flex;
  align-items: center;
}

.todo__loading {
  padding: 0px 10px;
}

::-webkit-scrollbar {
  height: 3px;
  width: 0px;
}

/* Track */

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: #fe6323;
  border-radius: 10px;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #a84014;
}

@media (max-width: 560px) {
  .todo__body {
    flex-direction: column;
    width: 100%;
  }
  .todo__content:not(:last-child) {
    max-width: 100%;
    margin-bottom: 10px;
    flex: 1 1 100%;
    align-self: flex-start;
  }
  .buttons {
    width: 100%;
    padding-top: 10px;
    padding-right: 5px;
    justify-content: center;
    border-top: 1px #fe65238a solid;
  }
  .todo__loading {
    padding: 0px;
  }
}
