.post__page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.post {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    border: 1px solid rgba(31, 31, 41, 0.7);
    border-radius: 10px;
    padding: 20px;
    margin: 40px 0px;
}

.post__header {
    font-size: 24px;
    padding: 0px 0px 20px 0px;
}

.post__content {
    padding: 0px 0px 30px 0px;
}

.post__title {
    padding: 0px 0px 10px 0px;
    font-weight: bold;
}

.post__body {
    text-align: justify;
    text-indent: 1.5em;
}

.comments__header {
    font-size: 18px;
    padding: 0px 0px 10px 0px;
}

@media (max-width: 560px) {
    .post {
        padding: 20px 0px;
        border-right: none;
        border-left: none;
        border-radius: 0px;
        width: 80%;
    }
}