.post {
  border: 1px solid rgba(31, 31, 41, 0.7);
  border-radius: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.post:not(:last-child) {
  margin: 0px 0px 10px 0px;
}

.post__header {
  font-weight: bold;
  display: flex;
}

.post__header:not(:last-child) {
  margin: 0px 0px 10px 0px;
}

.post__number:not(:last-child) {
  margin: 0px 10px 0px 0px;
}

.post__body {
  padding: 0px 0px 20px 0px;
  border-bottom: 1px #fe65238a solid;
  text-indent: 1.5em;
  text-align: justify;
}

.post__body:not(:last-child) {
  margin: 0px 0px 20px 0px;
}

.btn__container {
  align-self: flex-end;
}

@media (max-width: 560px) {
  .post {
    padding: 20px 0px;
    border-right: none;
    border-left: none;
    border-radius: 0px;
  }
  .btn__container {
    align-self: stretch;
  }
}
